@import '../../global.scss';

.intro {
  background-color: white;
  display: flex;

  @include mobile {
    flex-direction: column;
    align-items: center;
  }

  .left {
    flex: 0.5;

    .imageContainer {
      width: 450px;
      height: 450px;
      background-color: oldlace;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      float: right;
      img {
        height: 50%;
      }
    }
  }

  .right {
    flex: 0.5;
    position: relative;

    .wrapper {
      width: 100%;
      height: 100%;
      padding-left: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      h1 {
        font-size: 40px;
        margin: 10px 0;

        @include mobile {
          font-size: 20px;
        }
      }
      h2 {
        font-size: 35px;
        @include mobile {
          font-size: 25px;
        }
      }
      h3 {
        font-size: 25px;

        @include mobile {
          font-size: 20px;
        }

        span {
          font-size: inherit;
          color: crimson;
        }

        .ityped-cursor {
          animation: blink 1s infinite;
        }

        @keyframes blink {
          50% {
            opacity: 1;
          }
          100% {
            opacity: 0;
          }
        }
      }
    }

    a {
      position: absolute;
      bottom: 10px;
      left: 40%;

      img {
        width: 30px;
        animation: arrowBlink 2s infinite;
      }

      @keyframes arrowBlink {
        to {
          opacity: 0;
        }
      }
    }
  }
}
