@import '../../global.scss';

.portfolioList {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;

  @include mobile {
    margin-right: 20px;
  }
  li {
    font-size: 14px;
    margin-right: 50px;
    padding: 7px;
    border-radius: 20px;
    cursor: pointer;

    &.active {
      background-color: $mainColor;
      color: white;
    }
  }
}
